.fade-in {
    opacity: 0;
    animation: fade-in-animation 1s ease-in-out forwards;
  }
  
  @keyframes fade-in-animation {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .about-container {
    max-width: 50%;
    margin: 0 auto;
    margin-top: 40px;
    padding: 60px;
    background-color: #f9f9f9;
    color: #4d4e4e;
    border-radius: 5px;
    font-family: poppins,Arial,sans-serif;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .about-title {
    font-size: 50%;
    color: #4d4e4e;
    font-family: poppins,Arial,sans-serif;
    margin-bottom: 20px;
  }
  
  .about-description {
    font-size: 18px;
    color: #555;
    line-height: 1.5;
    margin-bottom: 10px;
  }
  
  #about-description2 {
    font-weight: bold;
  }
  
  #about-description3 {
    font-style: italic;
  }
  
